import { Checkbox, Row, Col } from "antd";
import { Mobile, PC } from "../Info/MediaQuery";

const HeroOption = (props) => {
  function onChange(checkedValues) {
    console.log("checked = ", checkedValues);
    props.setOption(checkedValues);
  }
  return (
    <Checkbox.Group
      defaultValue={[
        "3",
        "2",
        "1",
        "",
        "",
        "",
        "",
        "",
        "",
      ]}
      style={{ width: "100%" }}
      onChange={onChange}
    >
      <PC>
        <Row>
          <Col span={2}>
            <Checkbox value="3">3</Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value="2">2</Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value="1">1</Checkbox>
          </Col>
        </Row>

        <br />

        <Row>
          <Col span={2}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={2}>
            <Checkbox value=""></Checkbox>
          </Col>
        </Row>
      </PC>

      <Mobile>
        <Row>
          <Col span={6}>
            <Checkbox value="3">3</Checkbox>
          </Col>
          <Col span={6}>
            <Checkbox value="2">2</Checkbox>
          </Col>
          <Col span={6}>
            <Checkbox value="1">1</Checkbox>
          </Col>
        </Row>

        <br />

        <Row>
          <Col span={6}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={6}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={6}>
            <Checkbox value=""></Checkbox>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span={6}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={6}>
            <Checkbox value=""></Checkbox>
          </Col>
          <Col span={6}>
            <Checkbox value=""></Checkbox>
          </Col>
        </Row>
      </Mobile>
    </Checkbox.Group>
  );
};

export default HeroOption;
